@font-face {
    font-family: 'sign';  /* project id 404297 */
    src: url('//at.alicdn.com/t/font_404297_g55ivyi9j1xg8pvi.eot');
    src: url('//at.alicdn.com/t/font_404297_g55ivyi9j1xg8pvi.eot?#iefix') format('embedded-opentype'),
    url('//at.alicdn.com/t/font_404297_g55ivyi9j1xg8pvi.woff') format('woff'),
    url('//at.alicdn.com/t/font_404297_g55ivyi9j1xg8pvi.ttf') format('truetype'),
    url('//at.alicdn.com/t/font_404297_g55ivyi9j1xg8pvi.svg#iconfont') format('svg');
}

.App {
    color: #333;
    width: 100vw;
    height: 100vh;
    min-height: 20em;
    min-width: 32em;
    /*background: url("./img/bgbg.png")   no-repeat;*/
    background-color: white;
    background-size: cover;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}

.App > aside {
    min-width: 300px;
    width: 15vw;
    height: 100vh;
    background: url("https://www.toptal.com/designers/subtlepatterns/patterns/carbon_fibre_v2.png") repeat;
    position: relative;
}

.App > main {
    min-width: 900px;
    width: 85vw;
    height: 100vh;
    background-image: url(data:image/png;base64,);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    overflow-y: auto;
}

.App .todoList > li {
    margin-bottom: 1em;
    border-radius: 0.75em;
}

.App .todoList > li:nth-child(odd) {
    background: rgba(159, 195, 194, 0.6);
}

.App .todoList > li:nth-child(even) {
    background: rgba(253, 221, 155, 0.6);
}

.App h1 {
    position: relative;
    font-size: 2.25em;
    font-weight: lighter;
    text-align: center;
}

#signOut {
    font-size: 2em;
    color: rgba(240, 66, 66, .6);
    letter-spacing: .15em;
    line-height: 1.5em;
    width: 100%;
    padding: .3em;
    position: absolute;
    bottom: 0.75em;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    overflow: hidden;
    outline: none;
    border: none;
    background: transparent;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-transition: .8s ease all;
    -o-transition: .8s ease all;
    transition: .8s ease all;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

#signOut > span:first-child {
    font-family: sign;
}

#signOut > span:last-child {
    font-family: "Microsoft Yahei", sans-serif;
    font-size: .8em;
    /*font-weight: lighter;*/
}

#signOut:hover {
    color: #f02b2b;
    background: rgba(0, 0, 0, .3);
    border: 2px dashed rgba(255, 255, 255, .3);
}



